.groups {
  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 24px;
    line-height: 34px;
    color: #fff;

    margin-bottom: 8px;
  }
  
  &__photo-container {
    position: relative;

    box-sizing: border-box;
    width: 100%;
    max-height: 180px;
    overflow: hidden;
    
    margin-bottom: 16px;

    &::after {
      content: "";
      display: block;
      padding-bottom: 100%;
    }

    @include desktop {
      height: 320px;
      max-height: 320px;
      padding-bottom: 0;
    }

    @include tablet {
      height: 190px;
      padding-bottom: 0;
    }
  }

  &__photo {
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;

    object-fit: cover;
    object-position: center;
    transition: transform 300ms;
    transform-origin: center;

    &:hover {
      transform: scale(120%);
    }
  }

  &__text {
    margin: 0;
    
    margin-bottom: 40px;
  }
}
