.bike {

  &__photo {
    display: block;
    margin: 0 auto 32px;
    width: 280px;
    height: 230px;
    transition: transform 300ms;
    transform-origin: center;

    &:hover {
      transform: scale(120%);
    }
  }

  &__about {
    margin-left: 20px;
  }

  &__name {
    margin: 0 0 8px;
    font-weight: 700;
    font-size: 24px;
    line-height: 32px;
  }

  &__description {
    margin: 0 0 16px;
  }

  &__price {
    font-weight: 700;
    font-size: 24px;
    line-height: 32px;

    margin-bottom: 48px;
  }
}
