.button {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 100%;
  height: 56px;

  text-decoration: none;
  background: #fff;
  border-radius: 40px;
  cursor: pointer;
  color: $menuTextColor;

  transition: all 300ms;

  &--primary {
    font-weight: 700;
    font-size: 16px;
    line-height: 16px;
    box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.4);
  }

  &:hover {
    box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.4);
  }

  &:active {
    box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.64);
  }
}
