.contacts {
  &__info {
    margin: 0;

    &--title {
      margin: 0 0 8px;
      font-weight: 400;
      font-size: 14px;
      line-height: 18px;
    }

    &--link {
      display: block;
      margin: 0 0 24px;
      color: #fff;
      text-decoration: none;
      transition: color 300ms;

      &:hover {
        color: rgb(220, 91, 91);
      }

      &:last-child {
        margin-bottom: 0;
      }
    }

    &__field {
      margin: 0 0 24px;

      &--last {
        margin-bottom: 32px;
      }
    }
  }
}
